<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="ruleForm"
      :model="entity"
      :rules="rules"
      :label-col="{ span: 5 }"
      :wrapper-col="{ span: 14 }"
    >
    <div class="card">
        <a-row :gutter="24">
            <a-col :sm="6">
      <!-- 基本信息 -->
      <a-form-model-item label="工厂编码" prop="factoryCode">
        <a-input
          v-model="entity.factoryCode"
          placeholder="请输入工厂编码"
          @change="(e) => handleChange('factoryCode', e.target.value)"
        />
      </a-form-model-item>
    </a-col>
    <a-col :sm="6">
      <a-form-model-item label="工厂名称" prop="name">
        <a-input
          v-model="entity.name"
          placeholder="请输入工厂名称"
          @change="(e) => handleChange('name', e.target.value)"
        />
      </a-form-model-item>
    </a-col>
    <a-col :sm="6">
      <a-form-model-item label="工厂类型" prop="factoryType">
        <a-select
          placeholder="请选择工厂类型"
          v-model="entity.factoryType"
          @change="(value) => handleChange('factoryType', value)"
        >
          <a-select-option value="生产工厂">生产工厂</a-select-option>
          <a-select-option value="组装工厂">组装工厂</a-select-option>
          <a-select-option value="加工工厂">加工工厂</a-select-option>
          <a-select-option value="储存中心">储存中心</a-select-option>
          <a-select-option value="物流中心">物流中心</a-select-option>
        </a-select>
      </a-form-model-item>
    </a-col>
    <a-col :sm="6">
      <a-form-model-item label="所属企业" prop="enterpriseId">
        <a-select
          placeholder="请选择所属企业"
          v-model="entity.enterpriseId"
          @change="(value) => handleChange('enterpriseId', value)"
        >
          <a-select-option value="1">制造业企业1</a-select-option>
          <a-select-option value="2">电子信息企业2</a-select-option>
          <a-select-option value="3">汽车制造企业3</a-select-option>
          <a-select-option value="4">医药制造企业4</a-select-option>
          <a-select-option value="5">食品加工企业5</a-select-option>
        </a-select>
      </a-form-model-item>
    </a-col>
    <a-col :sm="6">
      <a-form-model-item label="建立日期" prop="foundDate">
        <a-date-picker
          v-model="entity.foundDate"
          format="YYYY-MM-DD"
          style="width: 100%"
          @change="(date, dateString) => handleChange('foundDate', dateString)"
        />
      </a-form-model-item>
    </a-col>
    <a-col :sm="6">
      <a-form-model-item label="工厂面积" prop="area">
        <a-input
          v-model="entity.area"
          placeholder="请输入工厂面积"
          @change="(e) => handleChange('area', e.target.value)"
        >
          <template #addonAfter>平方米</template>
        </a-input>
      </a-form-model-item>
    </a-col>
    <a-col :sm="6">
      <a-form-model-item label="生产线数量" prop="productionLines">
        <a-input-number
          v-model="entity.productionLines"
          :min="0"
          style="width: 100%"
          @change="(value) => handleChange('productionLines', value)"
        />
      </a-form-model-item>
    </a-col>
    <a-col :sm="6">
      <a-form-model-item label="生产能力" prop="capacity">
        <a-input
          v-model="entity.capacity"
          placeholder="请输入生产能力"
          @change="(e) => handleChange('capacity', e.target.value)"
        />
      </a-form-model-item>
    </a-col>
    <a-col :sm="6">
      <a-form-model-item label="工厂地址" prop="address">
        <a-textarea
          v-model="entity.address"
          placeholder="请输入工厂地址"
          :auto-size="{ minRows: 2, maxRows: 6 }"
          @change="(e) => handleChange('address', e.target.value)"
        />
      </a-form-model-item>
    </a-col>
    <a-col :sm="6">
      <a-form-model-item label="工厂负责人" prop="manager">
        <a-input
          v-model="entity.manager"
          placeholder="请输入工厂负责人"
          @change="(e) => handleChange('manager', e.target.value)"
        />
      </a-form-model-item>
    </a-col>
    <a-col :sm="6">
      <a-form-model-item label="联系电话" prop="contact">
        <a-input
          v-model="entity.contact"
          placeholder="请输入联系电话"
          @change="(e) => handleChange('contact', e.target.value)"
        />
      </a-form-model-item>
    </a-col>
    <a-col :sm="6">
      <a-form-model-item label="运行状态" prop="status">
        <a-select
          placeholder="请选择运行状态"
          v-model="entity.status"
          @change="(value) => handleChange('status', value)"
        >
          <a-select-option value="运行中">运行中</a-select-option>
          <a-select-option value="筹建中">筹建中</a-select-option>
          <a-select-option value="维护中">维护中</a-select-option>
          <a-select-option value="停产">停产</a-select-option>
        </a-select>
      </a-form-model-item>
    </a-col>
    <a-col :sm="6">
      <a-form-model-item label="描述" prop="description">
        <a-textarea
          v-model="entity.description"
          placeholder="请输入描述"
          :auto-size="{ minRows: 2, maxRows: 6 }"
          @change="(e) => handleChange('description', e.target.value)"
        />
      </a-form-model-item>
      </a-col>
    </a-row>

    </div>


      <!-- 生产线信息 -->
      <g-collapse headerName="生产线信息">
          
          <template #content>
            <a-button type="primary" @click="addProductionLine">
            <a-icon type="plus" />新增生产线
          </a-button>
        <a-table
          :columns="productionLineColumns"
          :data-source="entity.productionLines"
          rowKey="id"
          :pagination="false"
          :scroll="{ y: 300 }"
          style="margin-top: 10px;"
        >
          <template #sequence="{ text, record, index }">
            {{ index + 1 }}
          </template>
          <template #lineCode="{ text, record }">
            <a-input
              v-model="record.lineCode"
              placeholder="请输入生产线编码"
              @change="(e) => updateProductionLine(record, 'lineCode', e.target.value)"
            />
          </template>
          <template #lineName="{ text, record }">
            <a-input
              v-model="record.lineName"
              placeholder="请输入生产线名称"
              @change="(e) => updateProductionLine(record, 'lineName', e.target.value)"
            />
          </template>
          <template #lineType="{ text, record }">
            <a-select
              placeholder="请选择生产线类型"
              v-model="record.lineType"
              style="width: 100%"
              @change="(value) => updateProductionLine(record, 'lineType', value)"
            >
              <a-select-option value="组装线">组装线</a-select-option>
              <a-select-option value="包装线">包装线</a-select-option>
              <a-select-option value="生产线">生产线</a-select-option>
              <a-select-option value="检测线">检测线</a-select-option>
            </a-select>
          </template>
          <template #capacity="{ text, record }">
            <a-input
              v-model="record.capacity"
              placeholder="请输入产能"
              @change="(e) => updateProductionLine(record, 'capacity', e.target.value)"
            />
          </template>
          <template #operation="{ text, record }">
            <a-button
              type="danger"
              size="small"
              @click="removeProductionLine(record)"
            >
              <a-icon type="delete" />移除
            </a-button>
          </template>
        </a-table>
    </template>

      </g-collapse>

      <!-- 设备信息 -->
      <g-collapse headerName="设备信息">
        <template #content>
          <a-button type="primary" @click="addEquipment">
            <a-icon type="plus" />新增设备
          </a-button>
        <a-table
          :columns="equipmentColumns"
          :data-source="entity.equipments"
          rowKey="id"
          :pagination="false"
          :scroll="{ y: 300 }"
        >
          <template #sequence="{ text, record, index }">
            {{ index + 1 }}
          </template>
          <template #equipCode="{ text, record }">
            <a-input
              v-model="record.equipCode"
              placeholder="请输入设备编码"
              @change="(e) => updateEquipment(record, 'equipCode', e.target.value)"
            />
          </template>
          <template #equipName="{ text, record }">
            <a-input
              v-model="record.equipName"
              placeholder="请输入设备名称"
              @change="(e) => updateEquipment(record, 'equipName', e.target.value)"
            />
          </template>
          <template #equipType="{ text, record }">
            <a-select
              placeholder="请选择设备类型"
              v-model="record.equipType"
              style="width: 100%"
              @change="(value) => updateEquipment(record, 'equipType', value)"
            >
              <a-select-option value="生产设备">生产设备</a-select-option>
              <a-select-option value="检测设备">检测设备</a-select-option>
              <a-select-option value="辅助设备">辅助设备</a-select-option>
              <a-select-option value="运输设备">运输设备</a-select-option>
            </a-select>
          </template>
          <template #lineId="{ text, record }">
            <a-select
              placeholder="请选择所属生产线"
              v-model="record.lineId"
              style="width: 100%"
              @change="(value) => updateEquipment(record, 'lineId', value)"
            >
              <a-select-option 
                v-for="line in entity.productionLines" 
                :key="line.id" 
                :value="line.id"
              >
                {{ line.lineName }}
              </a-select-option>
            </a-select>
          </template>
          <template #status="{ text, record }">
            <a-select
              placeholder="请选择设备状态"
              v-model="record.status"
              style="width: 100%"
              @change="(value) => updateEquipment(record, 'status', value)"
            >
              <a-select-option value="运行中">运行中</a-select-option>
              <a-select-option value="空闲中">空闲中</a-select-option>
              <a-select-option value="维护中">维护中</a-select-option>
              <a-select-option value="故障">故障</a-select-option>
            </a-select>
          </template>
          <template #operation="{ text, record }">
            <a-button
              type="danger"
              size="small"
              @click="removeEquipment(record)"
            >
              <a-icon type="delete" />移除
            </a-button>
          </template>
        </a-table>
    </template>

      </g-collapse>

      <!-- 详细信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
        <a-row :gutter="16">
          <a-col :span="12">
            <a-form-model-item label="创建人">
              <a-input v-model="entity.createdBy" disabled />
            </a-form-model-item>
          </a-col>
          <a-col :span="12">
            <a-form-model-item label="创建时间">
              <a-input v-model="entity.createdTime" disabled />
            </a-form-model-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="12">
            <a-form-model-item label="修改人">
              <a-input v-model="entity.modifiedBy" disabled />
            </a-form-model-item>
          </a-col>
          <a-col :span="12">
            <a-form-model-item label="修改时间">
              <a-input v-model="entity.modifiedTime" disabled />
            </a-form-model-item>
          </a-col>
        </a-row>
    </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";

export default {
  name: "CreateOrEditFactory",
  mixins: [GNdoPageEditBase],
  data() {
    return {
        entity:{},
        loading:false,
      rules: {
        factoryCode: [
          { required: true, message: "请输入工厂编码", trigger: "blur" },
          { pattern: /^FC\d{4}$/, message: "工厂编码必须为FC+4位数字", trigger: "blur" }
        ],
        name: [
          { required: true, message: "请输入工厂名称", trigger: "blur" }
        ],
        factoryType: [
          { required: true, message: "请选择工厂类型", trigger: "change" }
        ],
        enterpriseId: [
          { required: true, message: "请选择所属企业", trigger: "change" }
        ],
        foundDate: [
          { required: true, message: "请选择建立日期", trigger: "change" }
        ],
        area: [
          { required: true, message: "请输入工厂面积", trigger: "blur" }
        ],
        productionLines: [
          { required: true, message: "请输入生产线数量", trigger: "change" },
          { type: "number", message: "生产线数量必须为数字", trigger: "change" }
        ],
        capacity: [
          { required: true, message: "请输入生产能力", trigger: "blur" }
        ],
        address: [
          { required: true, message: "请输入工厂地址", trigger: "blur" }
        ],
        manager: [
          { required: true, message: "请输入工厂负责人", trigger: "blur" }
        ],
        contact: [
          { required: true, message: "请输入联系电话", trigger: "blur" },
          { pattern: /^1\d{10}$/, message: "请输入正确的手机号码", trigger: "blur" }
        ],
        status: [
          { required: true, message: "请选择运行状态", trigger: "change" }
        ]
      },
      productionLineColumns: [
        {
          title: "序号",
          dataIndex: "sequence",
          key: "sequence",
          width: 80,
          scopedSlots: { customRender: "sequence" }
        },
        {
          title: "生产线编码",
          dataIndex: "lineCode",
          key: "lineCode",
          width: 150,
          scopedSlots: { customRender: "lineCode" }
        },
        {
          title: "生产线名称",
          dataIndex: "lineName",
          key: "lineName",
          width: 150,
          scopedSlots: { customRender: "lineName" }
        },
        {
          title: "生产线类型",
          dataIndex: "lineType",
          key: "lineType",
          width: 150,
          scopedSlots: { customRender: "lineType" }
        },
        {
          title: "产能",
          dataIndex: "capacity",
          key: "capacity",
          width: 150,
          scopedSlots: { customRender: "capacity" }
        },
        {
          title: "操作",
          key: "operation",
          width: 100,
          fixed: "right",
          scopedSlots: { customRender: "operation" }
        }
      ],
      equipmentColumns: [
        {
          title: "序号",
          dataIndex: "sequence",
          key: "sequence",
          width: 60,
          scopedSlots: { customRender: "sequence" }
        },
        {
          title: "设备编码",
          dataIndex: "equipCode",
          key: "equipCode",
          width: 120,
          scopedSlots: { customRender: "equipCode" }
        },
        {
          title: "设备名称",
          dataIndex: "equipName",
          key: "equipName",
          width: 150,
          scopedSlots: { customRender: "equipName" }
        },
        {
          title: "设备类型",
          dataIndex: "equipType",
          key: "equipType",
          width: 120,
          scopedSlots: { customRender: "equipType" }
        },
        {
          title: "所属生产线",
          dataIndex: "lineId",
          key: "lineId",
          width: 150,
          scopedSlots: { customRender: "lineId" }
        },
        {
          title: "设备状态",
          dataIndex: "status",
          key: "status",
          width: 120,
          scopedSlots: { customRender: "status" }
        },
        {
          title: "操作",
          key: "operation",
          width: 100,
          fixed: "right",
          scopedSlots: { customRender: "operation" }
        }
      ],
    };
  },
  created() {
    // 初始化entity实体，避免页面报错
    this.initializeEntity();
  },
  methods: {
    initializeEntity() {
      if (!this.entity) {
        this.entity = {
          factoryCode: "",
          name: "",
          factoryType: "",
          enterpriseId: undefined,
          enterpriseName: "",
          foundDate: "",
          area: "",
          productionLines: [],
          equipments: [],
          capacity: "",
          address: "",
          manager: "",
          contact: "",
          status: "筹建中",
          description: "",
          createdBy: "admin",
          createdTime: new Date().toLocaleString(),
          modifiedBy: "admin",
          modifiedTime: new Date().toLocaleString()
        };
      }
    },
    handleChange(field, value) {
      // 处理表单字段变化
      this.entity[field] = value;
      
      // 联动处理
      if (field === 'enterpriseId') {
        const enterprises = [
          {id: 1, name: "制造业企业1"},
          {id: 2, name: "电子信息企业2"},
          {id: 3, name: "汽车制造企业3"},
          {id: 4, name: "医药制造企业4"},
          {id: 5, name: "食品加工企业5"}
        ];
        
        const enterprise = enterprises.find(e => e.id == value);
        if (enterprise) {
          this.entity.enterpriseName = enterprise.name;
        }
      }
    },
    addProductionLine() {
      // 添加生产线
      if (!Array.isArray(this.entity.productionLines)) {
        this.entity.productionLines = [];
      }
      
      const lineCount = this.entity.productionLines.length + 1;
      this.entity.productionLines.push({
        id: Date.now(),
        lineCode: `PL${String(lineCount).padStart(3, "0")}`,
        lineName: `生产线${lineCount}`,
        lineType: "生产线",
        capacity: "100件/小时",
      });
    },
    updateProductionLine(record, field, value) {
      // 更新生产线信息
      record[field] = value;
    },
    removeProductionLine(record) {
      // 移除生产线
      const index = this.entity.productionLines.findIndex(item => item.id === record.id);
      if (index !== -1) {
        this.entity.productionLines.splice(index, 1);
        
        // 更新关联的设备数据
        if (this.entity.equipments) {
          this.entity.equipments.forEach(equipment => {
            if (equipment.lineId === record.id) {
              equipment.lineId = undefined;
            }
          });
        }
      }
    },
    addEquipment() {
      // 添加设备
      if (!Array.isArray(this.entity.equipments)) {
        this.entity.equipments = [];
      }
      
      const equipCount = this.entity.equipments.length + 1;
      this.entity.equipments.push({
        id: Date.now(),
        equipCode: `EQ${String(equipCount).padStart(3, "0")}`,
        equipName: `设备${equipCount}`,
        equipType: "生产设备",
        lineId: this.entity.productionLines && this.entity.productionLines.length > 0 
          ? this.entity.productionLines[0].id 
          : undefined,
        status: "运行中",
      });
    },
    updateEquipment(record, field, value) {
      // 更新设备信息
      record[field] = value;
    },
    removeEquipment(record) {
      // 移除设备
      const index = this.entity.equipments.findIndex(item => item.id === record.id);
      if (index !== -1) {
        this.entity.equipments.splice(index, 1);
      }
    },
    save() {
      // 保存表单
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          console.log("表单校验通过，开始保存", this.entity);
          
          // 模拟保存，实际项目中应替换为实际的保存逻辑
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.$message.success("保存成功");
            
            // 更新修改时间和修改人
            this.entity.modifiedTime = new Date().toLocaleString();
            this.entity.modifiedBy = "admin";
            
            // 如果是新增，还需要设置创建时间和创建人
            if (!this.isEdit) {
              this.entity.createdTime = new Date().toLocaleString();
              this.entity.createdBy = "admin";
            }
          }, 1000);
          
          return true;
        } else {
          console.log("表单校验失败");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.ant-form-item {
  margin-bottom: 12px;
}
</style> 